.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}
.dialog {
    padding: 5px;
}
.dialog a {
    text-decoration: none;
    color: #454545;
}
.dialog a.active{
    color: #000;
}
.dialog img {
    width: 40px;
    border-radius: 50%;
    float: left;
}
.dialog p {
    margin-left: 60px;
}

.messages {
    border-left: 1px solid #cecece;
    padding: 15px;
}


.message {
    padding: 10px;

}
.message span{
    background-color: #6395ed;
    color: #fff;
    padding: 5px 10px;
    border-radius: 8px;
    box-shadow: 0.4em 0.4em 5px rgba(122,122,122,0.5);
}
.message:nth-child(1n) span:before {
    border: 10px solid transparent;
    border-bottom-color: #6295ed;
    position: relative;
    bottom: 100%;
    right: -10px;
    content: '';
    top: 3px;
    float: left;

}
.message:nth-child(2n) span:before {
    border: 10px solid transparent;
    border-bottom-color: #eda963;
    position: relative;
    bottom: 100%;
    right: 10px;
    content: '';
    top: 3px;
    float: right;
}
.message:nth-child(2n) {
    text-align: right;
}
.message:nth-child(2n) span {
    background-color: #eda963;
}

textarea {
    width: 100%;
    margin-top: 20px;
}