.formControl {

}

.formControl.error input,
.formControl.error textarea{
    border: 1px solid red;
}

.formControl.error span {
    color: red;

}

textarea {
    padding: 10px;
    font-family: Roboto;
    font-size: 14px;
    width: 98%!important;
    border: 0px;
    margin-bottom: 20px;

}
button {
    width: auto!important;
    float: right;
    background-color: #6395ed;
    border: 0px;
    padding: 10px 20px!important;
    color: #fff;
    border-radius: 50px;
}
